form {
    margin-bottom: 1em;
}
.form-inline .hz-form-group {
    background-color: #dfdfdf;
    border-radius: 4px;
    padding: 10px;
    margin-right: 10px;
    margin-bottom: 10px !important;
    border-radius: 4px;
    position: relative;
}
.form-inline .hz-form-group a {
    position: absolute;
    top: 9px;
    right: 10px;
}
@media (min-width: 768px) {
    .form-inline .form-group .hz-form-control {
        display: block !important;
    }
}



